<template>
  <div class="home">
    <div v-if="noData">
      <van-cell :title="project.projectname" is-link to="/projectSelect" />
      <div class="gridMain">
        <van-grid :gutter="10" :column-num="2">
          <van-grid-item class="tag" to="/project">
            <van-image
              width="60"
              round
              :src="require('@/assets/img/img1.png')"
            />
            <p class="title">项目信息</p>
          </van-grid-item>
          <van-grid-item class="tag" to="/jobs">
            <van-image
              width="60"
              round
              :src="require('@/assets/img/img2.png')"
            />
            <p class="title">务工经历</p>
          </van-grid-item>
          <van-grid-item class="tag" to="/attendance">
            <van-image
              width="60"
              round
              :src="require('@/assets/img/img3.png')"
            />
            <p class="title">考勤信息</p>
          </van-grid-item>
          <van-grid-item class="tag" to="/wages">
            <van-image
              width="60"
              round
              :src="require('@/assets/img/img4.png')"
            />
            <p class="title">工资信息</p>
          </van-grid-item>
        </van-grid>
      </div>
    </div>
    <div v-else>
      <van-empty description="未查询到项目信息" />
    </div>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: "Home",
  components: {},
  data() {
    return {
      project: "", //localStorage.getItem('project')
      noData:true,
    };
  },
  methods: {
    getProjectListByEmployeeId() {
      this.$http.jsb.getProjectListByEmployeeId().then((res) => {
        if (res.code == 200) {
          if (res.data.length > 0) {
            localStorage.setItem("project", JSON.stringify(res.data[0]));
            this.project = JSON.parse(localStorage.getItem("project"));
          } else {
            this.noData = false
          }
        }
      });
    },
  },
  mounted() {
    if (!localStorage.getItem("project")) {
      this.getProjectListByEmployeeId();
    } else {
      this.project = JSON.parse(localStorage.getItem("project"));
    }
  },
};
</script>
<style lang="less">
.home {
  font-size: 20px;
  .gridMain {
    padding-top: 10px;
    .van-grid-item__content {
      border-radius: 10px;
      overflow: hidden;
      p {
        font-size: 15px;
        color: #8f8f8f;
        margin-top: 5px;
      }
    }
  }
}
</style>